import React from 'react';

export interface IButtonProps {
  /**
   * Set the cta style
   */
  cta?: boolean;
  /**
   * Set the primary style
   */
  primary?: boolean;

  /**
   * Set the secondary style
   */
  secondary?: boolean;
  /**
   * Set the interactive style
   */
  interactive?: boolean;
  /**
   * Set the icon style
   */
  icon?: {
    value: string;
    position: 'left' | 'right';
  };
  /**
   * Set the fullWidth style
   */
  fullWidth?: boolean;
  /**
   * Additional or alternative styling
   */
  className?: string;
  /**
   * Set the small style
   */
  small?: boolean;
  /**
   * Set the large style
   */
  large?: boolean;
  [others: string]: any;
}

const Button: React.FC<IButtonProps> = ({
  cta,
  primary,
  secondary,
  interactive,
  fullWidth,
  icon,
  className,
  children,
  small,
  large,
  ...rest
}) => {
  return (
    <button
      type="button"
      className={`${
        cta
          ? 'button-cta'
          : primary
          ? 'button-primary'
          : secondary
          ? 'button-secondary'
          : interactive
          ? 'button-interactive'
          : 'button'
      } ${small ? 'small' : large ? 'large' : ''} ${fullWidth ? 'full-width' : ''}
    ${className || ''}`}
      {...rest}
    >
      {icon && icon.position === 'left' && <span className="material-icons left">{icon?.value}</span>}
      {children}
      {icon && icon.position === 'right' && <span className="material-icons right">{icon?.value}</span>}
    </button>
  );
};

export default Button;
